import View from './view.js';
import * as model from '../model.js';
import resultsView from './resultsView.js';

class SearchView extends View{
    _parentElement=document.querySelector('.hero-section__container');
    _searchButton=document.querySelector('.hero-section__container__search-button');
    _searchBar=document.querySelector('.hero-section__container__search-bar')
    _query='';
    _shortCircuit=0;


   
  
    addHandlerSearch(handler){
        const searchBar=this._searchBar;
        
        this._searchButton.addEventListener('click',function(e){
            console.log('aceptation');
            let query=this._searchBar;
            e.preventDefault();

                handler(searchBar.value);
                model.clearAppState();
                resultsView.clearParent();
        });
    }

    addHandlerSubmit(handler){
        const searchBar=this._searchBar;

        this._searchBar.addEventListener('keypress',function(e){
            if(e.key==='Enter'){
                e.preventDefault();
                let query=this._searchBar;

                handler(searchBar.value);
                model.clearAppState();
                resultsView.clearParent();
            };
        });
        
    };

   
    addHandlerMobileNav(handler){
        const openMobileButton=document.querySelector('.navigation__container__phone-nav');
        const closeMobileButton=document.querySelector('.mobile-nav__nav-close-button');
        openMobileButton.addEventListener('click',function(e){
            e.preventDefault()
            handler('open')
        })
        closeMobileButton.addEventListener('click',function(e){
            e.preventDefault();
            handler('close');
        })

    }

    toggleMobileMenue(action){

        const mobileMenue=document.querySelector('.mobile-nav');
        if(action==='open'){
            //openning//
            mobileMenue.classList.remove('mobile-menue-invisible')
            mobileMenue.classList.add('mobile-menue-visible');
        }else if(action==='close'){
           
            mobileMenue.classList.add('mobile-menue-invisible');

        }

    }

    iconsFunctions(imgId,action,alt){

        if(this._shortCircuit>0)
        return

        this.performImgActions(imgId,action,alt);
        this._shortCircuit++;


    };

    performImgActions(id,action,alt){
        if(action==='expand'){ 
            let bigImgUrl='';
            let modal=document.querySelector('.full-img-modal');
            let imgAlt='';
            //getting the full url//
            let iconsToShow=document.querySelectorAll('.action-icons');
            iconsToShow.forEach(function(el){
                el.classList.add('hidden-icon');
            })
        
            model.state.results.els.forEach((el,index,array)=>{
                el.imgId===id?bigImgUrl=el.imgUrlBig:'';
                el.imgId===id?imgAlt=el.imgAlt:'';


            })
           /*  <button class="save-to-fav-button"><ion-icon name="bookmark-outline"></ion-icon></button> */

            const markup=`  <div class="full-img-modal__container"> 
            <button class="full-img-modal__container__close-button"><ion-icon name="close-circle-outline"></ion-icon></button>
            <button class="full-img-modal__container__download-button"><ion-icon name="download-outline"></ion-icon></button>
        
            <img class="full-img-modal__container__img" src="${bigImgUrl}" alt="${imgAlt}" id="${id}">
            <!--  <ion-icon name="bookmark"></ion-icon> filled icon code-->
            </div>`;

            modal.insertAdjacentHTML('afterbegin',markup);
            modal.classList.remove('hidden-full-img-modal');

        
            this._shortCircuit=0;

            this.readIconsOnExpandedImg(id);


       }else if(action==='download'){
            let downloadLink='';
            let fileName='';
            let iconsToShow=document.querySelectorAll('.action-icons');
            iconsToShow.forEach(function(el){
                el.classList.add('hidden-icon');
            })
           
            //getting the full url//
            
            model.state.results.els.forEach((el,index,array)=>{
                el.imgId===id?downloadLink=el.imgFullLink:'';//////
                el.imgId===id?fileName=el.imgAlt:'';

            })

            this.executeDonwload(downloadLink,fileName);
           
            
         
       }else if(action==='save'){

        return;
        ///FYI this functionality is dicontinued due to lack of back end resources

       /*  let iconsToShow=document.querySelectorAll('.action-icons');
        iconsToShow.forEach(function(el){
            el.classList.add('hidden-icon');
        }) */
        //1read img id and alt▶️
      /*   console.log(id);
        console.log(alt); */
        //2 check if already saved//

        //SAVING TO LOCAL STORAGE//
        /* localStorage.setItem(id, alt); */

        //3read model resulst.els to find el

        //3.1 compare the id with els to find img

        //3.2 copy img object

        //4 push matching el to local storage
       };


    };

    readIconsOnExpandedImg(id){
        let closeButtons=document.querySelector('.full-img-modal__container__close-button');
        let parentModalContainer=document.querySelector('.full-img-modal');
        let downloadButton=document.querySelector('.full-img-modal__container__download-button');
        

        closeButtons.addEventListener('click',function(e){
            e.preventDefault();
            parentModalContainer.innerHTML='';
            parentModalContainer.classList.add('hidden-full-img-modal');
            
 
        });

        downloadButton.addEventListener('click',function(e){
            e.preventDefault()
            let downloadLink='';
            let fileName='';
           
            //getting the full url//
            
            model.state.results.els.forEach((el,index,array)=>{
                el.imgId===id?downloadLink=el.imgFullLink:'';//////
                el.imgId===id?fileName=el.imgAlt:'';

            })

            resultsView.downloadImgFromFullView(downloadLink,fileName);
        })


    }

    async getBlob(url){
        return fetch(url)
      .then(response => response.blob());
    }

    downLoadBlob(blob, filename){
        let url = URL.createObjectURL(blob);
  
            // Create an anchor element and set its attributes
            let a = document.createElement("a");
            a.href = url;
            a.download = filename;
        
            // Append the anchor to the document and click it
            document.body.appendChild(a);
            a.click();
        
            // Remove the anchor and revoke the URL
            document.body.removeChild(a);
            URL.revokeObjectURL(url);
    };

    executeDonwload(imgUrl,filename){

        this.getBlob(imgUrl)
         .then(blob => resultsView.downLoadBlob2(blob, filename))/* this.downloadBlob(blob, filename)) */
         .catch(error => console.error(error));

    };

    noImagesError(){
        const markup=`
        <div class="error">
        <p class="error__text">No images found for this query</p>
        <button class="error__button">Try Again!</button>
        </div>`

        this._parentElement.insertAdjacentHTML('afterbegin',markup);
        this.addHandlerErrorMessageButton();
    }

    addHandlerErrorMessageButton(){
        const button=document.querySelector('.error__button')
        const errorModal=document.querySelector('.error');
        const inputField=document.querySelector('.hero-section__container__search-bar');


        button.addEventListener('click',function(e){
            e.preventDefault()
            errorModal.remove();
            inputField.value='';
            


            
        })

        
    }

  
};

export default new SearchView();
