import * as model from './model.js';
import searchView from './views/searchView.js';
import resultsView from './views/resultsView.js';

import 'core-js/stable';
import 'regenerator-runtime/runtime';
import { async } from 'regenerator-runtime';


const controlSearchAndRender=async function(query){ 
   try{ 
   ///search results//
await model.loadResults(query);
   //render results//

      if(model.state.results.els[0]===undefined){
           searchView.noImagesError();
           return;

      }else{ 
      
      resultsView.displayOrHideImgContainer();
     
      await resultsView.render();
      
      resultsView.displayLoadMoreButton('display');

      await searchView.scrollToSection();  
   }

 }catch(err){
   console.log(err);
 }
}

const loadMoreFunction=function(){
   resultsView.displayMoreImages();
   
}

const goUp=function(){
  searchView.scrollToSection(resultsView._sectionZero,'30rem');
}

const showImageButtonIcons=function(action){
   //searchview. show

   //search view/ hide
}

const mobileMenue=function(action){
   searchView.toggleMobileMenue(action);
}

const iconsActionsController=function(id,action){
   searchView.iconsFunctions(id,action);

}




///❤️❤️❤️initializing❤️❤️❤️////
const init=function(){
   searchView.renderYear();
   //1 search and load results//
   searchView.addHandlerSearch(controlSearchAndRender);
   
   searchView.addHandlerSubmit(controlSearchAndRender);

   //2 load more functionality activated//
   resultsView.addHandlerLoadMore(loadMoreFunction);
   
   //3activate go up button//
   resultsView.addHandlerMoveUp(goUp);
   //4 activating buttons on images//
   resultsView.addHandlerHoverImages(showImageButtonIcons);
   //activate open close mobile menue//
   searchView.addHandlerMobileNav(mobileMenue);
   //activating icons in images to be able to do actions //
 /*   resultsView.addHandlerIcons(iconsActionsController); */



   
};


init(); 
