import * as configData from './config.js';
import resultsView from './views/resultsView.js';


export const state={
    query:'',
    results:{
        els:[],
    },
    page:1,
    resultsPerPage:'',
    searching:0, //if more that 0, app will restart search

};

export const loadResults= async function(query){
    try{

        resultsView.toggleSpiner('show');

        state.query=query;
        const apiKey='9g3Uv1WTLMWTZQORysZGOwkiMZaSi5LF6Ls3DIzCdHc'

        const response= await fetch(`https://api.unsplash.com/search/photos?page=${state.page}&query=${query}&client_id=${apiKey}`);
        const data= await response.json();
         
        console.log(data);

        let comparisonArray=[];



        let tempData={};

       
        tempData=data.results.map(el=>{
            return{
                imgid:el.id,
                imgUrl:el.urls.small,
                imgUrlBig:el.urls.regular,
                imgId:el.id,
                imgAlt:el.alt_description,
                imgDownloadLink:el.links.download,
                imgFullLink:el.urls.full,
                
            };
        })

        for (const [i, el] of tempData.entries()){
            state.results.els.push(el);
        };

    
            resultsView.toggleSpiner('hide');
            resultsView.readingImgsOnScreen();//>><<<<<<<<<<<<
            console.log(state.results.els);

        

    }catch(err){
        console.log(err);
        throw err;
    }
}

export const clearAppState=function(){
    state.results.els=[];
    state.page=1;
    state.searching=0;

}
