import View from './view.js';
import * as model from '../model.js';
import searchView from './searchView.js';

class ResultsView extends View {
    _parentElemenet=document.querySelector('.main__results-container');
    _parentElement=document.querySelector('.img-container-forced');
    _loadMoreButton=document.querySelector('.main__results-container__load-more-button');
    _goUpButton=document.querySelector('.main__results-container__go-up-button');
    _sectionZero=document.querySelector('.navigation');
    _spiner=document.querySelector('.main__results-container__spiner-container');
     _actionIcons='';
 
    _imgDisplayingOnScreen='';

      generateMarkup(data, imgSize){
      //1 clean container//
        this._parentElement.innerHTML='';
     
      //2 looping data received to extract info to generate markup
          const renderingEls=data.forEach(element => {

            //3 decide what img size to render//SMALL//
            if(imgSize==='small'){

              /*  <button class="img-container-forced__el__save-to-favorite action-icons hidden-icon" id="save"><ion-icon name="bookmark-outline"></ion-icon></button>  */
                  let markup=`
                  <div class="img-container-forced__el" id="${element.imgId}">
                    <button class="img-container-forced__el__expand-button action-icons hidden-icon " id="expand"><ion-icon name="expand-outline"></ion-icon></button>
                    <button class="img-container-forced__el__download-button action-icons hidden-icon" id="download"><ion-icon name="download-outline"></ion-icon></button>
                     <img class="img-container-forced__el__img" src="${element.imgUrl}" alt="${element.imgAlt}" >
                  </div>`
                  ///rendering markup
                  this._parentElement.insertAdjacentHTML('beforeend',markup); 
                  this.readingImgsOnScreen();

            }else if(imgSize=='big'){ //big images///


                  let markup=`
                  <div class="img-container-forced__el" id="${element.imgId}">
                  <button class="img-container-forced__el__expand-button action-icons hidden-icon " id="expand"><ion-icon name="expand-outline"></ion-icon></button>
                  <button class="img-container-forced__el__download-button action-icons hidden-icon" id="download"><ion-icon name="download-outline"></ion-icon></button>
                    <img class="img-container-forced__el__img" src="${element.imgUrlBig}" alt="${element.imgAlt}">
                  </div>`
                   ///rendering markup
                  this._parentElement.insertAdjacentHTML('beforeend',markup); 
                  this.readingImgsOnScreen();
            }
   
        })
        
     };

     clearParent(){
      this._parentElement.textContent='';
     
     }

     displayLoadMoreButton(action){
       if(action==='display'){
        this._loadMoreButton.classList.add('load-more-visible');
       }else if(action==='hide'){
        this._loadMoreButton.classList.remove('load-more-visible');
       }
    }

    displayOrHideImgContainer(action){
       /*  img-container-hidden */
       this._parentElemenet.classList.remove('img-container-hidden');//☀️TODO ADD HIDE VERSION OF THIS FUNCTION/

    }

    async displayMoreImages(){
      try{ 
         await model.state.page++;
        
         await model.loadResults(model.state.query);
     
         this.render(model.state.results.els);

      }catch(err){
      console.log(err);
      }

   }

    addHandlerLoadMore(handler){
       this._loadMoreButton.addEventListener('click',handler);
    }

    addHandlerMoveUp(handler){
      this._goUpButton.addEventListener('click',function(){
         handler(this._sectionZero);
         
      })
    }

    toggleSpiner(action){
      if(action==='show'){
         this._spiner.classList.remove('spiner-container-hidden');
      }else if(action==='hide'){
         this._spiner.classList.add('spiner-container-hidden');
      }
    }

   readingImgsOnScreen(){
      this._imgDisplayingOnScreen=document.querySelectorAll('.img-container-forced__el__img');
      this._actionIcons=document.querySelectorAll('.hidden-icon');

      this.addHandlerHoverImages();

    }

    

    addHandlerHoverImages(){
         
         const imgs=document.querySelectorAll('.img-container-forced__el__img')
         const icons=document.querySelectorAll('.hidden-icon');
         let divId=false;
         let curIcons='';
         let imgIdtest='';

         
     
         imgs.forEach(function(el){
            
            el.addEventListener('mouseover',function(e){
               searchView._shortCircuit=0;
             
               e.preventDefault();
              
               let imgHovered=e.target;
               let curId=e.target.parentNode.id;


               if(divId===false & curId !==divId){
                  console.log('a');
                 
                  let elsToShow=document.getElementById(`${curId}`);
                  let iconsToShow=elsToShow.querySelectorAll('.action-icons');
                  
                  iconsToShow.forEach(function(el){
                     el.classList.remove('hidden-icon');
                  }) 
                  divId=curId;
                 /*  curIcons=elsToShow; */
                  
               }else if(divId!==false & curId===divId){
                  searchView._shortCircuit=0;
                  console.log('b');
                  let elsToShow=document.getElementById(`${curId}`);
                  let iconsToShow=elsToShow.querySelectorAll('.action-icons');
                  
                  iconsToShow.forEach(function(el){
                     el.classList.remove('hidden-icon');
                  }) 
                 
            
                  //DO NOTHING//
               } else if(divId!==false & curId!==divId){
                  console.log('c');
                  searchView._shortCircuit=0;
                  //1

                  let prevelsToShow=document.getElementById(`${divId}`);
                 
                  let iconsToHide=prevelsToShow.querySelectorAll('.action-icons');
                 
                  
                  iconsToHide.forEach(function(el){
                     el.classList.add('hidden-icon');
                  }) 

                  //2
                  divId=curId;
                  //3

                  let elsToShow=document.getElementById(`${curId}`);
                  let iconsToShow=elsToShow.querySelectorAll('.action-icons');
                  
                  iconsToShow.forEach(function(el){
                     el.classList.remove('hidden-icon');

                  }) 

                  divId=curId;
               } 
            })
         })

         ///❤️❤️❤️❤️❤️ buttons/////

          icons.forEach(function(el){
            el.addEventListener('click',function(e){
               e.preventDefault();
              
               let action=e.target.parentNode.id;
               let imgClicked=e.target.parentNode.parentNode.querySelector('.img-container-forced__el__img')
               let imgidi=imgClicked.parentNode.id;
               

               let imgAlt=imgClicked.alt;
               
               searchView.iconsFunctions(imgidi,action,imgAlt);

            }); 
           
         })   
    }

    downLoadBlob2(blob, filename){
      let url = URL.createObjectURL(blob);

          // Create an anchor element and set its attributes
          let a = document.createElement("a");
          a.href = url;
          a.download = filename;
      
          // Append the anchor to the document and click it
          document.body.appendChild(a);
          a.click();
      
          // Remove the anchor and revoke the URL
          document.body.removeChild(a);
          URL.revokeObjectURL(url);
  };

  downloadImgFromFullView(downloadLink,fileName){
   searchView.executeDonwload(downloadLink,fileName);
  }



};

export default new ResultsView();